import React, { Component } from "react";
import "./Home.css";
import Data from "./data.json";
import Suggestion from "../Components/Suggestion";
import Tabular from "../Components/Tabular";
export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: Data.data,
      keyword: "",
      results: [],
      ShowSugg: false,
      ShowDetail: false,
    };
  }
  matchName = (fname, keyword) => {
    let keylen = keyword.length;
    keyword = keyword.toLowerCase();
    fname = fname.toLowerCase().substring(0, keylen);
    return fname === keyword && keylen !== 0;
  };
  onSearch = (value) => {
    let results = this.state.data.filter((item) =>
      this.matchName(item.fname, value)
    );
    this.setState({ results: results });
  };
  updateField = (value) => {
    this.setState({
      keyword: value,
      ShowSugg: true,
    });
    this.onSearch(value);
  };
  updateText = (fname, student_id) => {
    let res = this.state.data.filter(
      (item) => item.fname === fname && item.student_id === student_id
    );
    this.setState({
      keyword: fname,
      ShowSugg: false,
      results: res,
    });
  };
  clear = () => {
    this.setState({
      keyword: "",
      ShowSugg: false,
      results: [],
    });
  };
  handleClick = () => {
    this.setState({
      ShowDetail: true,
    });
  };
  closeDetails = () => {
    this.setState({
      ShowDetail: false,
      results: [],
      keyword: "",
      ShowSugg: false,
    });
  };
  render() {
    return (
      <div className="App">
        {this.state.ShowDetail ? (
          <Tabular results={this.state.results} onclick={this.closeDetails} />
        ) : (
          <div className="auto-complete-container">
            <h1 className="main-head">Student Details</h1>
            <br />
            <br />
            <br />
            <div className="search-container">
              <div className="input">
                <input
                  className="search-bar"
                  placeholder="Search..."
                  onChange={(event) => this.updateField(event.target.value)}
                  value={this.state.keyword}
                />
                {this.state.keyword.length > 0 ? (
                  <div className="button">
                    <button className="clear" onClick={() => this.clear()}>
                      X
                    </button>
                  </div>
                ) : null}
              </div>
            </div>
            {this.state.ShowSugg && this.state.results.length > 0 ? (
              <div className="suggestion-container">
                <Suggestion
                  results={this.state.results}
                  update={this.updateText}
                />
              </div>
            ) : this.state.keyword.length > 0 &&
              this.state.results.length === 0 ? (
              <div className="no-results">
                <h1>No Results Found</h1>
                <br />
                <h4> Try Different Keywords</h4>
              </div>
            ) : null}
            {this.state.results.length === 1 &&
            this.state.keyword.toLowerCase() ===
              this.state.results[0].fname.toLowerCase() ? (
              <div className="details">
                <button
                  className="detailButton"
                  onClick={() => this.handleClick()}>
                  GetDetails
                </button>
              </div>
            ) : null}
          </div>
        )}
      </div>
    );
  }
}
