import React from "react";
import "./Suggestion.css";
function Suggestion(props) {
  const sugg = props.results.map((data, index) => {
    return (
      <div
        className="stndata"
        key={index}
        onClick={() => props.update(data.fname, data.student_id)}>
        <span className="large">
          {data.fname}
          {"  "}
          {data.mname}
          {"  "} {data.lname}
        </span>
        <span className="right small">ID:{data.student_id}</span>
        <br />
        <span className="small">
          {data.grade}
          {"  "}
          {data.division}
        </span>
      </div>
    );
  });

  return <div className="suggestions">{sugg}</div>;
}

export default Suggestion;
