import React from "react";
import "./Tabular.css";

function Tabular(props) {
  const data = props.results.map((d, index) => {
    return (
      <table key={index}>
        <thead>
          <tr>
            <td colspan="2" className="bold">
              {d.fname} {d.mname} {d.lname}Details
            </td>
          </tr>
          <tr>
            <td colspan="2" className="bold">
              {d.schoolname}{" "}
            </td>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td className="bold">Grade</td>
            <td>{d.grade}</td>
          </tr>
          <tr>
            <td className="bold">Grade</td>
            <td>{d.division}</td>
          </tr>
          <tr>
            <td className="bold">Grade</td>
            <td>{d.student_id}</td>
          </tr>
          <tr>
            <td className="bold">Date of Birth</td>
            <td>{d.dob}</td>
          </tr>
          <tr>
            <td className="bold">Admission Date</td>
            <td>{d.Dofad}</td>
          </tr>
          <tr>
            <td className="bold">Gender</td>
            <td>{d.gender}</td>
          </tr>
          <tr>
            <td className="bold">Grade</td>
            <td>{d.grade}</td>
          </tr>
          <tr>
            <td className="bold">Father Name</td>
            <td>{d.fathername}</td>
          </tr>
          <tr>
            <td className="bold">Father Mobile Number</td>
            <td>{d.fathernumber}</td>
          </tr>
          <tr>
            <td className="bold">Mother Name</td>
            <td>{d.mothername}</td>
          </tr>
          <tr>
            <td className="bold">Mother Mobile Number</td>
            <td>{d.mothernumber}</td>
          </tr>
          <tr>
            <td className="bold">Guardian Name</td>
            <td>{d.guardname}</td>
          </tr>
          <tr>
            <td className="bold">Guardian Mobile Number</td>
            <td>{d.guardnumer}</td>
          </tr>
        </tbody>
      </table>
    );
  });
  return (
    <div className="outerdiv">
      <div className="innerdiv">
        <h2 className="close" onClick={() => props.onclick()}>
          &#10060;
        </h2>
        {data}
      </div>
    </div>
  );
}

export default Tabular;
